import React, { useState } from "react";
import { useReactMediaRecorder } from "react-media-recorder";
import SpeechRecognition, {
    useSpeechRecognition,
} from 'react-speech-recognition';

interface UploadResponse {
  message: string;
  uuid?: string;
  busy: boolean;
}

const uploadFile = async (blobUrl: string, blob: Blob): Promise<void> => {
    const uuid = crypto.randomUUID();

    const audioBlob = await fetch(blobUrl).then((r) => r.blob());
    const audioFile = new File([audioBlob], uuid + '.wav', { type: 'audio/wav'});
    const formData = new FormData();
    formData.append("file", blob, uuid + ".wav");

    try {
      const res = await fetch("https://video-response-upload.herokuapp.com/video", {
        method: "POST",
        body: formData
      });

      if(res.status === 200) {
        const successMessage: UploadResponse = { message: "Upload complete. You may continue.", uuid: uuid, busy: false };
        window.parent.postMessage(successMessage, '*');
      } else {
        const errorMessage: UploadResponse = { message: "There was an error uploading your video.", busy: false };
        window.parent.postMessage(errorMessage, '*');
      }
    } catch (error) {
      const errorMessage: UploadResponse = { message: "There was an error uploading your video.", busy: false };
      window.parent.postMessage(errorMessage, '*');
    }
}

const RecordView: React.FC = () => {
    const { status, startRecording, stopRecording, mediaBlobUrl } =
        useReactMediaRecorder({ audio: true, onStop: uploadFile });

    const {
        transcript,
        finalTranscript,
        listening,
        resetTranscript,
        browserSupportsSpeechRecognition,
        isMicrophoneAvailable,
    } = useSpeechRecognition();

    const [isListening, setIsListing] = useState<boolean>(false);
    const [audioBlob, setAudioBlob] = useState<Blob | null>(null);

    const handleSwitch = async (): Promise<void> => {
        if (!isListening) {
            SpeechRecognition.startListening({ language: 'en' });
            startRecording();
        } else {
            await SpeechRecognition.stopListening();
            stopRecording();
            console.log(mediaBlobUrl);
            //await uploadFile(mediaBlobUrl);
        }
        setIsListing(!isListening);
    };

    return (
        <div>
            <p style={{ fontSize: '2rem' }}>Listening: {listening ? 'on' : 'off'}</p>
            <button
                onClick={handleSwitch}
                className="micb"
                style={{ padding: '1em', marginRight: '10px' }}
            >
                Start
            </button>
            <button
                onClick={handleSwitch}
                className="micb"
                style={{ padding: '1em', marginRight: '10px' }}
            >
                Stop
            </button>
            <button onClick={resetTranscript}>Reset</button>
            <form>
                <p>{isMicrophoneAvailable}</p>
                <p>{transcript}</p>
                <p>{finalTranscript}</p>
            </form>
            <p>{status}</p>
            <audio src={mediaBlobUrl} controls autoPlay loop />
        </div>
    );
};

export default RecordView; 