import React, { useEffect } from 'react';
import '../App.css';
import { VideoRecorder, createDefaultTheme,loadRedHatFonts } from '@gutcheck/video-recorder';
import { ThemeProvider } from '@mui/material/styles';
import { Typography } from '@mui/material';

interface UploadResponse {
  message: string;
  uuid?: string;
  busy: boolean;
  error?: boolean;
}

const redHatTheme = createDefaultTheme({
  palette: {
    primary: {
      main: '#000000',
    },
  }
});

const VideoRecorderPage: React.FC = () => {
  useEffect(() => {
    loadRedHatFonts();
  }, []);

  const handleSubmit = async (data: Blob, url: string): Promise<void> => {
    console.log("data",data, url);
    const message: UploadResponse = { message: "Please wait while the video is uploaded...", busy: true };
    window.parent.postMessage(message, '*');
    const uuid = crypto.randomUUID();

    const formData = new FormData();
    formData.append("file", data, uuid + ".webm");

    try {
      //const res = await fetch("http://localhost:3500/video", {
      const res = await fetch("https://video-response-upload.herokuapp.com/video", {
        method: "POST",
        body: formData
      });

      console.log("res",res, url);

      if(res.status === 200) {
        const successMessage: UploadResponse = { message: "Upload complete. You may continue.", uuid: uuid, busy: false, error: false };
        window.parent.postMessage(successMessage, '*');
      } else {
        const errorMessage: UploadResponse = { message: "There was an error uploading your video.", busy: false, error: true };
        window.parent.postMessage(errorMessage, '*');
      }
    } catch (error) {
      const errorMessage: UploadResponse = { message: "There was an error uploading your video.", busy: false, error: true };
      window.parent.postMessage(errorMessage, '*');
    }
  }

  return (
    <ThemeProvider theme={redHatTheme}> 
    <VideoRecorder
      onRecordingComplete={handleSubmit}
      maxDuration={120}
      showDownloadButton={false}
      enableBackgroundBlur={false}
      enableDeviceSelection={true}
      enableUpload={false}
      theme="redhat"
      aspectRatio={16/9}
      enableRotationCorrection={true}
    />
    </ThemeProvider>
  );
}

export default VideoRecorderPage; 